.item {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 10px solid #e1f5fe;
    box-sizing: border-box;
}
.day {
    margin: 0;
    font-size: 14px;
    margin-bottom: 10px;
}
.date {
    margin: 0;
    font-size: 24px;
    width: 2em;
    height: 2em;
    display: flex;
    justify-content: center;
    align-items: center;

}
.dateCurrent {
    background-color: #00b0ff;
    border-radius: 50%;
    color: #fff;
}