.dayOfWeek {
    border-bottom: 1px solid #c7c7c7;
}
.day {
    background-color: #fff;
}
.dayOfWeek + .dayOfWeek {
    border-left: 1px solid #c7c7c7 ;
}

.dayInField {
    margin: auto;
    text-align: center;
    font-size: 12px;
    padding-top: 3px;
    padding-bottom: 3px;
    background-color: #e3f2fd;

}
.dayInFieldCurrentMonth {
    background-color: #90caf9;
}
.dayInFieldActive {
    background-color: #2962ff;
    color: #ffffff;
    font-weight: 700;

}